<template>
  <fixed-layout>
    <template slot="header">
      <mt-header title="教练认证">
        <router-link to="/" slot="left">
          <mt-button icon="back" @click="$router.go(-1)"></mt-button>
        </router-link>
      </mt-header>
    </template>
    <mt-field type="tel" label="联系方式" placeholder="请填写正确的手机号码用于收取短信" class="right" v-model="phone"></mt-field>
    <mt-field type="mail" label="邮箱" placeholder="请填写邮箱" class="right" v-model="email"></mt-field>
    <mt-field label="所属城市" placeholder="请输入所在城市" class="right" v-model="city"></mt-field>
    <mt-field label="微信号" placeholder="请填写微信号" class="right" v-model="wxId"></mt-field>
    <mt-field type="number" label="银行账号" placeholder="请填写银行账号" class="right" v-model="bankNo"></mt-field>
    <mt-field type="text" label="所属机构" placeholder="请填写您所属的机构" class="right" v-model="institutionName"></mt-field>
    <mt-cell
      title="生日"
      :value="formatBirth"
      is-link
      @click.native="$refs.pickerBirth.open()">
    </mt-cell>
    <mt-datetime-picker
      :startDate="new Date('1900')"
      ref="pickerBirth"
      type="date"
      v-model="birth">
    </mt-datetime-picker>
    <mt-field type="text" label="语言" placeholder="请填写熟悉的语言" class="right" v-model="lang"></mt-field>
    <mt-cell title="上传头像"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="headimg.url||headimg.base64"
            @load="(e,file)=>handLoadImg(e,file,'headimg')"
            @delete="handDeleteImage(headimg)">
          </file-image2>
        </li>
      </ul>
    </section>
    <mt-cell title="个人主页图" value="请上传横图"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="mainimg.url||mainimg.base64"
            @load="(e,file)=>handLoadImg(e,file,'mainimg')"
            @delete="handDeleteImage(mainimg)">
          </file-image2>
        </li>
      </ul>
    </section>
    <mt-cell title="个人简介"></mt-cell>
    <mt-field type="textarea" placeholder="请简单介绍下自己，会有更多人对您感兴趣哦" v-model="desc"></mt-field>
    <mt-cell title="上传认证材料" value="请上传您的身份证正反面和教练证正反面"></mt-cell>
    <section class="m-uploads">
      <ul>
        <li>
          <file-image2
            :src="idcardF.url||idcardF.base64"
            @load="(e,file)=>handLoadImg(e,file,'idcardF')"
            @delete="handDeleteImage(idcardF)">
          </file-image2>
          <h4>身份证正面</h4>
        </li>
        <li>
          <file-image2
            :src="idcardB.url||idcardB.base64"
            @load="(e,file)=>handLoadImg(e,file,'idcardB')"
            @delete="handDeleteImage(idcardB)">
          </file-image2>
          <h4>身份证反面</h4>
        </li>
        <li>
          <file-image2
            :src="coachF.url||coachF.base64"
            @load="(e,file)=>handLoadImg(e,file,'coachF')"
            @delete="handDeleteImage(coachF)">
          </file-image2>
          <h4>教练证正面</h4>
        </li>
        <li>
          <file-image2
            :src="coachB.url||coachB.base64"
            @load="(e,file)=>handLoadImg(e,file,'coachB')"
            @delete="handDeleteImage(coachB)">
          </file-image2>
          <h4>教练证反面</h4>
        </li>
      </ul>
    </section>
    <section class="ft">
      <p class="agreement"><input type="checkbox" v-model="agree"/><a href="">同意《号角平台商户服务协议》</a></p>
      <mt-button type="primary" @click="handSubmit">确认</mt-button>
    </section>
  </fixed-layout>
</template>

<script>
  import { mapState } from 'vuex'
  import FixedLayout from '@/components/fixed-layout'
  import FileImage2 from '@/components/file-image2.vue'

  export default {
    name: 'coach',
    components: {
      FixedLayout,
      FileImage2,
    },
    data () {
      return {
        city: '',
        phone: null,
        email: '',
        wxId: '',
        bankNo: '',
        institutionName: '',
        birth: new Date(),
        lang: '',
        desc: '',
        headimg: {
          file: null,
          base64: '',
          url: ''
        },
        mainimg: {
          file: null,
          base64: '',
          url: ''
        },
        idcardF: {
          file: null,
          base64: '',
          url: ''
        },
        idcardB: {
          file: null,
          base64: '',
          url: ''
        },
        coachF: {
          file: null,
          base64: '',
          url: ''
        },
        coachB: {
          file: null,
          base64: '',
          url: ''
        },
        agree: false,
      }
    },
    computed: {
      ...mapState({}),
      formatBirth () {
        let y = this.birth.getFullYear(), m = this.birth.getMonth() + 1, d = this.birth.getDate()
        return `${y}-${m < 10 ? 0 : ''}${m}-${d < 10 ? 0 : ''}${d}`
      }
    },
    watch: {},
    created () {
      new Promise((resolve, reject) => {
        if (this.$route.query.coachId) {
          this.$http.get(this.$port.AUTH_COACH, {
            params: {
              coachId: this.$route.query.coachId
            }
          }).then(res => {
            if (res.code === 200) {
              this.city = res.data.city
              this.phone = res.data.phone
              this.email = res.data.email
              this.wxId = res.data.wxId
              this.bankNo = res.data.bankNo
              this.institutionName = res.data.institutionName
              this.birth = new Date(res.data.birth)
              this.lang = res.data.lang
              this.desc = res.data.desc
              this.headimg.url = res.data.headimg
              this.mainimg.url = res.data.mainimg
              this.idcardF.url = res.data.idcardF
              this.idcardB.url = res.data.idcardB
              this.coachF.url = res.data.coachF
              this.coachB.url = res.data.coachB
            }
            resolve()
          })
        } else {
          let name = 'coach_'
          this.city = localStorage.getItem(`${name}city`)
          this.phone = localStorage.getItem(`${name}phone`)
          this.email = localStorage.getItem(`${name}email`)
          this.wxId = localStorage.getItem(`${name}wxId`)
          this.bankNo = localStorage.getItem(`${name}bankNo`)
          this.institutionName = localStorage.getItem(`${name}institutionName`)
          this.lang = localStorage.getItem(`${name}lang`)
          this.desc = localStorage.getItem(`${name}desc`)
          resolve()
        }
      }).then(() => {
        let name = 'coach_'
        setInterval(() => {
          localStorage.setItem(`${name}city`, this.city || '')
          localStorage.setItem(`${name}phone`, this.phone || '')
          localStorage.setItem(`${name}email`, this.email || '')
          localStorage.setItem(`${name}wxId`, this.wxId || '')
          localStorage.setItem(`${name}bankNo`, this.bankNo || '')
          localStorage.setItem(`${name}institutionName`, this.institutionName || '')
          localStorage.setItem(`${name}lang`, this.lang || '')
          localStorage.setItem(`${name}desc`, this.desc || '')
        }, 1000)
      })
    },
    methods: {
      handDeleteImage (obj) {
        obj.file = null
        obj.base64 = ''
        obj.url = ''
      },
      uploadImg (file) {
        return new Promise((resolve, reject) => {
          let fd = new FormData()
          fd.append('imgType', 1)
          fd.append('upload', file)
          this.$http.post(this.$port.UPLOAD_IMG, fd).then(res => {
            resolve(res)
          })
        })
      },
      handLoadImg (e, file, prop) {
        console.log('handLoadImg', e, file, prop)
        this[prop].file = file
        this[prop].base64 = e.currentTarget.result
        this.uploadImg(file).then(res => {
          if (res.code === 200) {
            this[prop].url = res.data
          } else {
            alert(res.msg)
          }
        })
      },
      handSubmit () {
        if (!this.agree) {
          alert('同意服务协议才能提供服务')
          return
        }
        let params = {
          city: this.city,
          phone: this.phone,
          email: this.email,
          wxId: this.wxId,
          bankNo: this.bankNo,
          institutionName: this.institutionName,
          birth: this.formatBirth,
          lang: this.lang,
          headimg: this.headimg.url,
          mainimg: this.mainimg.url,
          idcardF: this.idcardF.url,
          idcardB: this.idcardB.url,
          coachF: this.coachF.url,
          coachB: this.coachB.url,
          desc: this.desc,
        }
        this.$http.post(this.$port.AUTH_COACH, this.$qs.stringify(params)).then(res => {
          if (res.code === 200) {

          }
          alert(res.msg)
        })
      }
    }
  }
</script>
<style lang="scss">
</style>
<style lang="scss" scoped>
  .m-uploads {
    background-color: #fff;
    padding: 0 10px;
    ul {
      display: flex;
      flex-wrap: wrap;
      li {
        position: relative;
        flex: 1;
        b {
          background-color: rgba(0, 0, 0, 0.5);
          text-align: center;
          font-weight: 400;
          font-size: 8px;
          color: #fff;
          width: 20px;
          height: 20px;
          position: absolute;
          right: 0;
          top: 0;
        }
        h4 {
          font-size: 12px;
          text-align: center;
          font-weight: 400;
          color: #333;
          padding-top: 5px;
        }
      }
    }
    .ui-upload {
      width: 110px;
      height: 110px;
    }
  }

  .uploadImage {
    width: 110px;
    height: 110px;
    border: solid #e5e5e5 1px;
    position: relative;
    img {
      width: 100%;
      height: 100%;
      object-fit: contain;
      display: block;
    }
  }

  .ft {
    padding: 15px 10px;
    border-top: solid #e5e5e5 1px;
    .agreement {
      font-size: 12px;
      margin-bottom: 10px;
      input[type='checkbox'] {
        margin-right: 5px;
        vertical-align: middle;
      }
    }
    .mint-button {
      width: 100%;
    }
  }
</style>



